// import Engine from './utils/engine';
import Engine from './utils/engine3d';

import { SphereGeometry, MeshBasicMaterial, Mesh, EdgesGeometry, LineBasicMaterial, LineSegments } from 'three';
import anime from 'animejs/lib/anime.es.js';

const WIDTH = 480;
const HEIGHT = 480;

// const el = document.getElementById('canvas');
const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT,
  pixelRatio: 2,
}, {
  alpha: true
});

const colours = [0x2A398D, 0xE76A7D, 0x1B184C, 0xF6D89A];

const createBall = (i) => {
  // const geometry = new EdgesGeometry(new SphereGeometry(0.5, 6, 6)); // or WireframeGeometry( geometry )
  // const material = new LineBasicMaterial( { color: colours[Math.floor(Math.random() * colours.length)], linewidth: 2 } );
  // const shape = new LineSegments( geometry, material );
  const geometry = new SphereGeometry(0.5, 20, 20); // or WireframeGeometry( geometry )
  const material = new MeshBasicMaterial( { color: colours[Math.floor(Math.random() * colours.length)] } );
  const shape = new Mesh( geometry, material );

  const rand = () => Math.round(anime.random(-10, 10));

  const pos = { x: rand(), y: rand(), z: rand() };
  shape.position.set(pos.x, pos.y, pos.z);

  let rotateX = (Math.random() - 0.5) / 50;
  let rotateY = (Math.random() - 0.5) / 50;
  let rotateZ = (Math.random() - 0.5) / 50;

  function createAnimation() {
    const local = {
      ...pos
    };

    anime({
      targets: local,
      x: () => rand(),
      y: () => rand(),
      z: () => rand(),
      duration: 1000,
      complete: () => {
        pos.x = local.x;
        pos.y = local.y;
        pos.z = local.z;
        createAnimation()
      },
      update: () => {
        shape.position.x = local.x;
        shape.position.y = local.y;
        shape.position.z = local.z;
        shape.rotation.z += rotateX;
        shape.rotation.y += rotateY;
        shape.rotation.x += rotateZ;
      },
      easing: 'easeInOutQuint'
    });
  }

  // setTimeout(() => createAnimation(), i * 10);
  createAnimation();

  app.scene.add( shape );
}

for (let i = 0; i < 100; i++) {
  createBall(i);
}

app.onRender(({ frame, width, height, debug }) => {
});

app.start();
